text = `Madame Vauquer, ne de Coflans, est une vieille femme qui, depuis quarante ans, tient  Paris une pension bourgeoise tablie rue Neuve-Sainte-Genevive, entre le quartier latin et le faubourg Saint-Marceau. Cette pension, connue sous le nom de la Maison Vauquer, admet galement des hommes et des femmes, des jeunes gens et des vieillards, sans que jamais la mdisance ait attaqu les murs de ce respectable tablissement. Mais aussi depuis trente ans ne sy tait-il jamais vu de jeune personne, et pour quun jeune homme y demeure, sa famille doit-elle lui faire une bien maigre pension. Nanmoins, en 1819, poque  laquelle ce drame commence, il sy trouvait une pauvre jeune fille. En quelque discrdit que soit tomb le mot drame par la manire abusive et tortionnaire dont il a t prodigu dans ces temps de douloureuse littrature, il est ncessaire de lemployer ici : non que cette histoire soit dramatique dans le sens vrai du mot ; mais, luvre accomplie, peut-tre aura-t-on vers quelques larmes intra muros et extra. Sera-t-elle comprise au del de Paris ? le doute est permis. Les particularits de cette scne pleine dobservations et de couleurs locales ne peuvent tre apprcies quentre les buttes de Montmartre et les hauteurs de Montrouge, dans cette illustre valle de pltras incessamment prs de tomber et de ruisseaux noirs de boue ; valle remplie de souffrances relles, de joies souvent fausses, et si terriblement agite quil faut je ne sais quoi dexorbitant pour y produire une sensation de quelque dure. Cependant il sy rencontre  et l des douleurs que lagglomration des vices et des vertus rend grandes et solennelles :  leur aspect, les gosmes, les intrts, sarrtent et sapitoient ; mais limpression quils en reoivent est comme un fruit savoureux promptement dvor. Le char de la civilisation, semblable  celui de lidole de Jaggernat,  peine retard par un cur moins facile  broyer que les autres et qui enraye sa roue, la bris bientt et continue sa marche glorieuse. Ainsi ferez-vous, vous qui tenez ce livre dune main blanche, vous qui vous enfoncez dans un moelleux fauteuil en vous disant : Peut-tre ceci va-t-il mamuser. Aprs avoir lu les secrtes infortunes du pre Goriot, vous dnerez avec apptit en mettant votre insensibilit sur le compte de lauteur, en le taxant dexagration, en laccusant de posie. Ah ! sachez-le : ce drame nest ni une fiction, ni un roman. All is true, il est si vritable, que chacun peut en reconnatre les lments chez soi, dans son cur peut-tre.

La maison o sexploite la pension bourgeoise appartient  madame Vauquer. Elle est situe dans le bas de la rue Neuve-Sainte-Genevive,  lendroit o le terrain sabaisse vers la rue de lArbalte par une pente si brusque et si rude que les chevaux la montent ou la descendent rarement. Cette circonstance est favorable au silence qui rgne dans ces rues serres entre le dme du Val-de-Grce et le dme du Panthon, deux monuments qui changent les conditions de latmosphre en y jetant des tons jaunes, en y assombrissant tout par les teintes svres que projettent leurs coupoles. L, les pavs sont secs, les ruisseaux nont ni boue ni eau, lherbe crot le long des murs. Lhomme le plus insouciant sy attriste comme tous les passants, le bruit dune voiture y devient un vnement, les maisons y sont mornes, les murailles y sentent la prison. Un Parisien gare ne verrait l que des pensions bourgeoises ou des Institutions, de la misre ou de lennui, de la vieillesse qui meurt, de la joyeuse jeunesse contrainte  travailler. Nul quartier de Paris nest plus horrible, ni, disons-le, plus inconnu. La rue Neuve-Sainte-Genevive surtout est comme un cadre de bronze, le seul qui convienne  ce rcit, auquel on ne saurait trop prparer lintelligence par des couleurs brunes, par des ides graves ; ainsi que, de marche en marche, le jour diminue et le chant du conducteur se creuse, alors que le voyageur descend aux Catacombes. Comparaison vraie ! Qui dcidera de ce qui est plus horrible  voir, ou des curs desschs, ou des crnes vides ?

La faade de la pension donne sur un jardinet, en sorte que la maison tombe  angle droit sur la rue Neuve-Sainte-Genevive, o vous la voyez coupe dans sa profondeur. Le long de cette faade, entre la maison et le jardinet, rgne un cailloutis en cuvette, large dune toise, devant lequel est une alle sable, borde de graniums, de lauriers-roses et de grenadiers plants dans de grands vases eu faence bleue et blanche. On entre dans cette alle par une porte btarde, surmonte dun criteau sur lequel est crit : Maison Vauquer, et dessous Pension bourgeoise des deux sexes et autres. Pendant le jour, une porte  claire-voie, arme dune sonnette criarde, laisse apercevoir au bout du petit pav, sur le mur oppos  la rue, une arcade peinte en marbre vert par un artiste du quartier. Sous le renfoncement que simule cette peinture, slve une statue reprsentant lAmour.  voir le vernis caill qui la couvre, les amateurs de symboles y dcouvriraient peut-tre un mythe de lamour parisien quon gurit  quelques pas de l. Sous le socle, cette inscription  demi efface rappelle le temps auquel remonte cet ornement par lenthousiasme dont il tmoigne pour Voltaire, rentr dans Paris en 1777:

Qui que tu sois, voici ton matre
Il lest, le fut, ou le doit tre.

 la nuit tombante, la porte  claire-voie est remplace par une porte pleine. Le jardinet, aussi large que la faade est longue, se trouve encaiss par le mur de la rue et par le mur mitoyen de la maison voisine, le long de laquelle pend un manteau de lierre qui la cache entirement, et attire les yeux des passants par un effet pittoresque dans Paris. Chacun de ces murs est tapiss despaliers et de vignes dont les fructifications grles et poudreuses sont lobjet des craintes annuelles de madame Vauquer et de ses conversations avec les pensionnaires. Le long de chaque muraille, rgne une troite alle qui mne  un couvert de tilleuls, mot que madame Vauquer, quoique ne de Conflans, prononce obstinment tieuilles, malgr les observations grammaticales de ses htes. Entre les deux alles latrales est un carr dartichauds flanqu darbres fruitiers en quenouille, et bord doseille, de laitue ou de persil. Sous le couvert de tilleuls est plante une table ronde peinte en vert, et entoure de siges. L, durant les jours caniculaires, les convives assez riches pour se permettre de prendre du caf,viennent le savourer par une chaleur capable de faire clore des ufs. La faade, leve de trois tages et surmonte de mansardes, est btie en moellons et badigeonne avec cette couleur jaune qui donne un caractre ignoble  presque toutes les maisons de Paris. Les cinq croises perces  chaque tage ont de petits carreaux et sont garnies de jalousies dont aucune nest releve de la mme manire, en sorte que toutes leurs lignes jurent entre elles. La profondeur de cette maison comporte deux croises qui, au rez-de-chausse, ont pour ornement des barreaux en fer, grillags. Derrire le btiment est une cour large denviron vingt pieds, o vivent en bonne intelligence des cochons, des poules, des lapins, et au fond de laquelle slve un hangar  serrer le bois. Entre ce hangar et la fentre de la cuisine se suspend le garde-manger, au-dessous duquel tombent les eaux grasses de lvier. Cette cour a sur la rue Neuve-Sainte-Genevive une porte troite par o la cuisinire chasse les ordures de la maison en nettoyant cette sentine  grand renfort deau, sous peine de pestilence.

Naturellement destin  lexploitation de la pension bourgeoise, le rez-de-chausse se compose dune premire pice claire par les deux croises de la rue, et o lon entre par une porte-fentre. Ce salon communique  une salle  manger qui est spare de la cuisine par la cage dun escalier dont les marches sont en bois et en carreaux mis en couleur et frotts. Rien nest plus triste  voir que ce salon meubl de fauteuils et de chaises en toffe de crin  raies alternativement mates et luisantes. Au milieu se trouve une table ronde  dessus de marbre Sainte-Anne, dcore de ce cabaret en porcelaine blanche orne de filets dor effacs  demi, que lon rencontre partout aujourdhui. Cette pice, assez mal planchie, est lambrisse  hauteur dappui. Le surplus des parois est tendu dun papier verni reprsentant les principales scnes de Tlmaque, et dont les classiques personnages sont coloris. Le panneau dentre les croises grillages offre aux pensionnaires le tableau du festin donn au fils dUlysse par Calypso. Depuis quarante ans cette peinture excite les plaisanteries des jeunes pensionnaires, qui se croient suprieurs  leur position en se moquant du dner auquel la misre les condamne. La chemine en pierre, dont le foyer toujours propre atteste quil ne sy fait de feu que dans les grandes occasions, est orne de deux vases pleins de fleurs artificielles, vieillies et encages, qui accompagnent une pendule en marbre bleutre du plus mauvais got. Cette premire pice exhale une odeur sans nom dans la langue, et quil faudrait appeler lodeur de pension. Elle sent le renferm, le moisi, le rance ; elle donne froid, elle est humide au nez, elle pntre les vtements ; elle a le got dune salle o lon a dn ; elle pue le service, loffice, lhospice. Peut-tre pourrait-elle se dcrire si lon inventait un procd pour valuer les quantits lmentaires et nausabondes quy jettent les atmosphres catarrhales et sui generis de chaque pensionnaire, jeune ou vieux. Eh ! bien, malgr ces plates horreurs, si vous le compariez  la salle  manger, qui lui est contigu, vous trouveriez ce salon lgant et parfum comme doit ltre un boudoir. Cette salle, entirement boise, fut jadis peinte en une couleur indistincte aujourdhui, qui forme un fond sur lequel la crasse a imprim ses couches de manire  y dessiner des figures bizarres. Elle est plaque de buffets gluants sur lesquels sont des carafes chancres, ternies, des ronds de moir mtallique, des piles dassiettes en porcelaine paisse,  bords bleus, fabriques  Tournai. Dans un angle est place une bote  cases numrotes qui sert garder les serviettes, ou taches ou vineuses, de chaque pensionnaire. Il sy rencontre de ces meubles indestructibles, proscrits partout, mais placs l comme le sont les dbris de la civilisation aux Incurables. Vous y verriez un baromtre  capucin qui sort quand il pleut, des gravures excrables qui tent lapptit, toutes encadres en bois noir verni  filets dors ; un cartel en caille incruste de cuivre ; un pole vert, des quinquets dArgand o la poussire se combine avec lhuile, une longue table couverte en toile cire assez grasse pour quun factieux externe y crive son nom en se servant de son doigt comme de style, des chaises estropies, de petits paillassons piteux en sparterie qui se droule toujours sans se perdre jamais, puis des chaufferettes misrables  trous casss,  charnires dfaites, dont le bois se carbonise. Pour expliquer combien ce mobilier est vieux, crevass, pourri, tremblant, rong, manchot, borgne, invalide, expirant, il faudrait en faire une description qui retarderait trop lintrt de cette histoire, et que les gens presss ne pardonneraient pas. Le carreau rouge est plein de valles produites par le frottement ou par les mises en couleur. Enfin, l rgne la misre sans posie ; une misre conome, concentre, rpe. Si elle na pas de fange encore, elle a des taches ; si elle na ni trous ni haillons, elle va tomber en pourriture.`

